const buildKlookLink = (arrivalCity) => {
    //  
    let link = 'https://www.klook.com';
    link += '/ko/search/result/?query=';
  
  
    // arrivalCity    
    const arrivalMatch = arrivalCity.match(/([-]+)/);
    const arrivalCode = arrivalMatch[1];
    link += `${arrivalCode}`;

    return link;
};

export default buildKlookLink;


