const buildTripadLink = (arrivalCity) => {
    //  
    let link = 'https://www.tripadvisor.co.kr/';
    link += 'Search?searchSessionId=0008cc7d11413ee2.ssid&q=';
  
  
    // arrivalCity    
    const arrivalMatch = arrivalCity.match(/([-]+)/);
    const arrivalCode = arrivalMatch[1];
    link += `${arrivalCode}`;

    return link;
};

export default buildTripadLink;


